<template>
  <v-container fluid fill-height>
    <v-layout align-start justify-center>
      <v-flex sm12 md9 lg12 class="pa-5">
        <v-row cols="12">
          <v-col cols="12">
            <p class="display-1 font-weight-light	text-center pa-4 pb-0">
              {{ contentData.contentTitle }}
            </p>
            <p class="overline text-center">
              By <b>{{ contentData.contentWriter }}</b> |
              {{ contentData.contentTime }}
            </p>
            <v-container fluid>
              <v-row justify="center">
                <v-col
                  class="content"
                  md="12"
                  lg="12"
                  v-html="contentData.contentBody"
                >
                </v-col>
              </v-row>
            </v-container>
          </v-col>
        </v-row>
      </v-flex>
    </v-layout>
  </v-container>
  <!-- <v-container
    fluid
    full-height
  >
    <p class="display-1 font-weight-light	text-center pa-4 pb-0">{{ contentData.contentTitle }}</p>
    <p class="overline text-center">By <b>{{ contentData.contentWriter }}</b> | {{ contentData.contentTime }}</p>
    <v-container fluid>
      <v-row justify="center">
        <v-col
          class="content"
          md="12"
          lg="12"
          v-html="contentData.contentBody"
        >
        </v-col>
      </v-row>
    </v-container>
  </v-container> -->
</template>
<script>
import axios from "axios";

export default {
  data() {
    return {
      editorConfig: {
        // The configuration of the editor.
      },
      contentData: []
    };
  },
  methods: {
    toThaiDateString(date) {
      let monthNames = [
        "มกราคม",
        "กุมภาพันธ์",
        "มีนาคม",
        "เมษายน",
        "พฤษภาคม",
        "มิถุนายน",
        "กรกฎาคม",
        "สิงหาคม.",
        "กันยายน",
        "ตุลาคม",
        "พฤศจิกายน",
        "ธันวาคม"
      ];

      let year = date.getFullYear() + 543;
      let month = monthNames[date.getMonth()];
      let numOfDay = date.getDate();

      let hour = date
        .getHours()
        .toString()
        .padStart(2, "0");
      let minutes = date
        .getMinutes()
        .toString()
        .padStart(2, "0");
      let second = date
        .getSeconds()
        .toString()
        .padStart(2, "0");

      return (
        `${numOfDay} ${month} ${year} ` + `${hour}:${minutes}:${second} น.`
      );
    }
  },
  mounted() {
    let queryId = this.$route.params.id;
    axios
      .get(
        `https://farmtd.hrdi.or.th/farmdd/admin/api/content/getContentById.php?contentId=${queryId}`
      )
      .then(response => {
        console.log(response);
        if (response.data.content.length > 0) {
          let dateTime = new Date(response.data.content[0].contentTime);
          this.contentData = response.data.content[0];
          this.contentData.contentTime = this.toThaiDateString(dateTime);
          console.log(this.contentData);
        }
      })
      .catch(error => {
        console.log(error);
      });
  },
  created() {}
};
</script>
<style>
img .img-responsive {
  max-width: 100%;
  height: auto;
}

.img-responsive {
  display: block;
  max-width: 100% !important;
  height: auto;
}

table {
  max-width: 100% !important;
  overflow-x: auto !important;
}
</style>



